export default {
  namespace: 'zhongduanspace',
  state: {
    opened: false,
  },
  effects: {
    *getZhongduanxx({ payload }, { call, put }) {
  /*    let zhongduanData;
      const response = yield call(getzhongduan,payload);
      zhongduanData = response.result;*/
      console.log(payload);
      yield put({
        type: 'ztzhongduanData',
        payload: payload,
      });
    },
  },
  reducers: {
      ztzhongduanData(state, { payload }) {
        return { ...state,zhongduanReturn: payload };
      },
    setOpened(state) {
      return {
        ...state,
        opened: true,
      };
    },
  },
};
